import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { loadScript } from '../../../../libs/htmlutlis';

@Component({
  selector: 'app-weui',
  templateUrl: './weui.component.html',
  styleUrls: ['./weui.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class WeuiComponent implements OnInit {
  value;
  values;
  constructor(@Inject(DOCUMENT) private document: any) {}

  async ngOnInit() {
    if (!window['weui']) {
      await loadScript(
        'https://res.wx.qq.com/open/libs/weuijs/1.1.4/weui.min.js'
      );
      await loadScript('https://weui.io/example.js');
    }
  }

  click() {
    window['weui'].alert('alert');
  }
}
